<template>
  <div>
    <ul v-for="obj in userList">
      <div class="container">
        <div class="card" style="width: 18rem; display: inline-block">
          <img class="card-img-top" v-bind:src="obj.picture.large" alt="Card image cap">
          <div class="card-body">
            <h5 class="card-title">{{obj.name.title + obj.name.first + ` ` + obj.name.last}}</h5>
            <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
          </div>
          <ul class="list-group list-group-flush">
            <li class="list-group-item">Cras justo odio</li>
            <li class="list-group-item">Dapibus ac facilisis in</li>
            <li class="list-group-item">Vestibulum at eros</li>
          </ul>
          <div class="card-body">
            <a href="#" class="card-link">Card link</a>
            <a href="#" class="card-link">Another link</a>
          </div>
        </div>
      </div>
    </ul>
  </div>
</template>

<script>
	export default {
		name: "list",
    data () {
		  return {
		    userList: []
      }
    },
    methods: {
		  getUser () {
		    let vm = this
		    this.axios.get('https://randomuser.me/api/?results=50')
          .then(function (e) {
            console.log(e)
            console.log(e.data.results)
            vm.userList = e.data.results
          })
          .catch(function (e) {
            console.log(e)
          })
      }
    },
    mounted () {
		  this.getUser()
    }
	}

</script>

<style scoped>

</style>



